@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
html,body{margin:0;height:100%;min-height:100%}body{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.main-container{height:100vh;width:100vw}.header{-webkit-flex:none;flex:none;height:4rem;top:0;position:static;background-color:#dddee1;display:-webkit-flex;display:flex;padding:0 2rem;font-family:'Source Sans Pro', sans-serif;-webkit-align-items:center;align-items:center}.header h1{color:#2e3440;font-size:1.8rem;font-weight:600}.module-theme-okaidia{width:100%;min-height:25vh;max-height:45vh;overflow:auto}.module-theme-okaidia::-webkit-scrollbar-thumb{background-color:#60656e;border-radius:10px;max-width:10px !important}.module-theme-okaidia::-webkit-scrollbar{background:#272822;width:5px}.language-json{font-family:'Source Code Pro', sans-serif !important}pre{min-height:25vh;max-height:45vh}.box-input{display:-webkit-flex;display:flex;min-height:25vh;max-height:45vh;overflow:hidden}.input-handler{cursor:ns-resize;width:50px;height:12px;-webkit-justify-content:center;justify-content:center;background:#2e3440;border-radius:8px;z-index:10;overflow:hidden;display:-webkit-flex;display:flex;position:fixed}.input-handler span{display:inline-block;overflow:hidden;font-size:12px;font-weight:700;font-family:sans-serif;letter-spacing:1px;color:#b3b3b3;text-shadow:1px 0 1px #5a5a5a}.input-handler span::after{content:'≡'}.chart-container{margin:0 auto;display:-webkit-flex;display:flex;overflow:auto;height:50%}.chart-canvas{max-height:90%;min-height:45%;padding:2rem 2rem 0}.footer{height:4.5rem;bottom:0;-webkit-flex:none;flex:none;position:fixed;width:100vw;background-color:#dddee1;display:-webkit-flex;display:flex;padding:0 2.2rem;margin-top:0.5rem;-webkit-align-items:center;align-items:center}.footer button{background-color:#0c7cfb;padding:0.9rem;border-radius:0.2rem;border:none;font-size:0.7rem;text-transform:uppercase;color:white;cursor:pointer;transition:0.3s}.footer button:hover{background-color:#0a4e96;transition:0.3s}

